$black: #000;
$red: #d40000;
$font_family_1: 'Ubuntu Condensed', sans-serif;
$white: #fff;

.card {
	position: absolute;
	display: inline-block;
	left: -1.9375rem;
	top: -2.75rem;
	width: 3.875rem;
	height: 5.5rem;
	background-color: $white;
	border-radius: 4px;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
	cursor: default;
	&:before, &:after {
		position: absolute;
		font-size: 0.7rem;
		text-align: center;
		line-height: 0.7rem;
		font-family: $font_family_1;
		white-space: pre-line;
		width: 0.55rem;
		letter-spacing: -0.1rem;
	}
	&:before {
		top: 0.15rem;
		left: 0;
	}
	&:after {
		bottom: 0.1rem;
		right: 0;
		transform: rotate(180deg);
	}
	.face {
		height: 100%;
		background-position: 50% 50%;
		background-size: 100% 100%;
		background-repeat: no-repeat;
	}
	.back {
		position: absolute;
		background-image: url("#{$faces}/back.png");
		background-position: 50% 50%;
		background-size: 100% 100%;
		background-repeat: no-repeat;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
	}
}
.card.spades {
	color: $black;
}
.card.clubs {
	color: $black;
}
.card.joker {
	color: $black;
}
.card.hearts {
	color: $red;
}
.card.diamonds {
	color: $red;
}
.card.joker {
	&.rank1, &.rank2, &.rank3 {
		&:before {
			content: "J\a O\a K\a E\a R";
			letter-spacing: 0;
			font-size: 0.4rem;
			line-height: 0.4rem;
			padding: 0.15rem 0.05rem;
			width: 0.5rem;
		}
		&:after {
			content: "J\a O\a K\a E\a R";
			letter-spacing: 0;
			font-size: 0.4rem;
			line-height: 0.4rem;
			padding: 0.15rem 0.05rem;
			width: 0.5rem;
		}
	}
	&.rank1 .face {
			background-image: url("#{$faces}/4_1.svg");
	}
	&.rank2 .face {
		background-image: url("#{$faces}/4_2.svg");
	}
	&.rank3 .face {
		background-image: url("#{$faces}/4_3.svg");
	}
	&.rank3 {
		color: $red;
	}
}
